.popup {
  display: flex;
  pointer-events: none;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #00000080;
  opacity: 0;
  visibility: hidden;
  transition: visibility .3s, opacity .3s ease-in-out;
}
