.gallery {
  margin: 0;
  padding: 0;
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fit, 282px);
  column-gap: 17px;
  row-gap: 20px;
  list-style: none;
}

@media (max-width: 1100px) {
  .gallery {
    padding-left: 19px;
    padding-right: 19px;
  }
}