.profile__job {
  margin: 0;
  margin-top: 9px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-size: 18px;
}

@media (max-width: 1100px) {
  .profile__job {
    font-size: 14px;
  }
}

@media (max-width: 767px) {
  .profile__job {
    text-align: center;
  }
}